import React from 'react'
import PropTypes from 'prop-types'
import axios from 'axios';

const Card = ({name, atk, def, mana, uuid, id}) => {
    const onClick = (e) => {
        console.log(e);
        let card = {"name": name,  "attack": atk, "health": def, "mana_cost": mana }
        console.log(card);
        if(id == 1){ //Card in hand
            let url = `http://localhost:8080/game/games/${uuid}/board/cards`
            axios.post(`${url}`, card).then( response => { }); 
        }
    }
    return (
  
    <card 
        onClick={onClick} 
        className='card'>
        {name}
        <br></br>atk: {atk}
        <br></br>def: {def}
        <br></br>cost: {mana}
    </card>
    )
}

Card.propTypes = {
    name: PropTypes.string.isRequired,
    atk: PropTypes.number.isRequired,
    def: PropTypes.number.isRequired,
    mana:PropTypes.number.isRequired,
}

Card.defaultProps = {
    name: '',
    atk: '',
    def: '',
    mana: ''
}

export default Card